<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    {{count}}
    {{doneTodosCount1}}
    {{doneTodosCount2}}
  </div>
</template>

<script>
// import data from '../../data/data.json'

export default {
  name: 'Home',
  computed: {
    count () {
      return this.$store.state.count
    },
    doneTodosCount1 () {
      return this.$store.state.todoList.filter(todoList => todoList.done).length
    },
    doneTodosCount2 () {
      return this.$store.getters.doneTodosCount
    }
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    console.log(JSON.stringify(this.$store.getters.getUserInfo))
    // this.$store.commit('userInfo', data)
    // console.log(this.$store.state.count)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



